`, "",{"version":3,"sources":["webpack://./src/App.vue"],"names":[],"mappings":";AA6OA;CACA,WAAA;CACA,YAAA;CACA,aAAA;CACA,aAAA;CACA,sBAAA;CACA,YAAA;AACA;AAEA;CACA,WAAA;AACA;AAEA;CACA,YAAA;CACA,WAAA;AACA","sourcesContent":["<template>\r\n\t<div id=\"content\" class=\"app-duplicatefinder\">\r\n\t\t<AppNavigation>\r\n\t\t\t<AppNavigationNew v-if=\"!loading\"\r\n\t\t\t\t:text=\"t('duplicatefinder', 'New note')\"\r\n\t\t\t\t:disabled=\"false\"\r\n\t\t\t\tbutton-id=\"new-duplicatefinder-button\"\r\n\t\t\t\tbutton-class=\"icon-add\"\r\n\t\t\t\t@click=\"newNote\" />\r\n\t\t\t<ul>\r\n\t\t\t\t<AppNavigationItem v-for=\"note in notes\"\r\n\t\t\t\t\t:key=\"note.id\"\r\n\t\t\t\t\t:title=\"note.title ? note.title : t('duplicatefinder', 'New note')\"\r\n\t\t\t\t\t:class=\"{active: currentNoteId === note.id}\"\r\n\t\t\t\t\t@click=\"openNote(note)\">\r\n\t\t\t\t\t<template slot=\"actions\">\r\n\t\t\t\t\t\t<ActionButton v-if=\"note.id === -1\"\r\n\t\t\t\t\t\t\ticon=\"icon-close\"\r\n\t\t\t\t\t\t\t@click=\"cancelNewNote(note)\">\r\n\t\t\t\t\t\t\t{{\r\n\t\t\t\t\t\t\tt('duplicatefinder', 'Cancel note creation') }}\r\n\t\t\t\t\t\t</ActionButton>\r\n\t\t\t\t\t\t<ActionButton v-else\r\n\t\t\t\t\t\t\ticon=\"icon-delete\"\r\n\t\t\t\t\t\t\t@click=\"deleteNote(note)\">\r\n\t\t\t\t\t\t\t{{\r\n\t\t\t\t\t\t\t t('duplicatefinder', 'Delete note') }}\r\n\t\t\t\t\t\t</ActionButton>\r\n\t\t\t\t\t</template>\r\n\t\t\t\t</AppNavigationItem>\r\n\t\t\t</ul>\r\n\t\t</AppNavigation>\r\n\t\t<AppContent>\r\n\t\t\t<div v-if=\"currentNote\">\r\n\t\t\t\t<input ref=\"title\"\r\n\t\t\t\t\tv-model=\"currentNote.title\"\r\n\t\t\t\t\ttype=\"text\"\r\n\t\t\t\t\t:disabled=\"updating\">\r\n\t\t\t\t<textarea ref=\"content\" v-model=\"currentNote.content\" :disabled=\"updating\" />\r\n\t\t\t\t<input type=\"button\"\r\n\t\t\t\t\tclass=\"primary\"\r\n\t\t\t\t\t:value=\"t('duplicatefinder', 'Save')\"\r\n\t\t\t\t\t:disabled=\"updating || !savePossible\"\r\n\t\t\t\t\t@click=\"saveNote\">\r\n\t\t\t</div>\r\n\t\t\t<div v-else id=\"emptycontent\">\r\n\t\t\t\t<div class=\"icon-file\" />\r\n\t\t\t\t<h2>{{\r\n\t\t\t\t t('duplicatefinder', 'Create a note to get started') }}</h2>\r\n\t\t\t</div>\r\n\t\t</AppContent>\r\n\t</div>\r\n</template>\r\n\r\n<script>\r\nimport ActionButton from '@nextcloud/vue/dist/Components/ActionButton'\r\nimport AppContent from '@nextcloud/vue/dist/Components/AppContent'\r\nimport AppNavigation from '@nextcloud/vue/dist/Components/AppNavigation'\r\nimport AppNavigationItem from '@nextcloud/vue/dist/Components/AppNavigationItem'\r\nimport AppNavigationNew from '@nextcloud/vue/dist/Components/AppNavigationNew'\r\n\r\nimport '@nextcloud/dialogs/styles/toast.scss'\r\nimport { generateUrl } from '@nextcloud/router'\r\nimport { showError, showSuccess } from '@nextcloud/dialogs'\r\nimport axios from '@nextcloud/axios'\r\n\r\nexport default {\r\n\tname: 'App',\r\n\tcomponents: {\r\n\t\tActionButton,\r\n\t\tAppContent,\r\n\t\tAppNavigation,\r\n\t\tAppNavigationItem,\r\n\t\tAppNavigationNew,\r\n\t},\r\n\tdata() {\r\n\t\treturn {\r\n\t\t\tnotes: [],\r\n\t\t\tcurrentNoteId: null,\r\n\t\t\tupdating: false,\r\n\t\t\tloading: true,\r\n\t\t}\r\n\t},\r\n\tcomputed: {\r\n\t\t/**\r\n\t\t * Return the currently selected note object\r\n\t\t * @returns {Object|null}\r\n\t\t */\r\n\t\tcurrentNote() {\r\n\t\t\tif (this.currentNoteId === null) {\r\n\t\t\t\treturn null\r\n\t\t\t}\r\n\t\t\treturn this.notes.find((note) => note.id === this.currentNoteId)\r\n\t\t},\r\n\r\n\t\t/**\r\n\t\t * Returns true if a note is selected and its title is not empty\r\n\t\t * @returns {Boolean}\r\n\t\t */\r\n\t\tsavePossible() {\r\n\t\t\treturn this.currentNote && this.currentNote.title !== ''\r\n\t\t},\r\n\t},\r\n\t/**\r\n\t * Fetch list of notes when the component is loaded\r\n\t */\r\n\tasync mounted() {\r\n\t\ttry {\r\n\t\t\tconst response = await axios.get(generateUrl('/apps/duplicatefinder/notes'))\r\n\t\t\tthis.notes = response.data\r\n\t\t} catch (e) {\r\n\t\t\tconsole.error(e)\r\n\t\t\tshowError(t('notestutorial', 'Could not fetch notes'))\r\n\t\t}\r\n\t\tthis.loading = false\r\n\t},\r\n\r\n\tmethods: {\r\n\t\t/**\r\n\t\t * Create a new note and focus the note content field automatically\r\n\t\t * @param {Object} note Note object\r\n\t\t */\r\n\t\topenNote(note) {\r\n\t\t\tif (this.updating) {\r\n\t\t\t\treturn\r\n\t\t\t}\r\n\t\t\tthis.currentNoteId = note.id\r\n\t\t\tthis.$nextTick(() => {\r\n\t\t\t\tthis.$refs.content.focus()\r\n\t\t\t})\r\n\t\t},\r\n\t\t/**\r\n\t\t * Action tiggered when clicking the save button\r\n\t\t * create a new note or save\r\n\t\t */\r\n\t\tsaveNote() {\r\n\t\t\tif (this.currentNoteId === -1) {\r\n\t\t\t\tthis.createNote(this.currentNote)\r\n\t\t\t} else {\r\n\t\t\t\tthis.updateNote(this.currentNote)\r\n\t\t\t}\r\n\t\t},\r\n\t\t/**\r\n\t\t * Create a new note and focus the note content field automatically\r\n\t\t * The note is not yet saved, therefore an id of -1 is used until it\r\n\t\t * has been persisted in the backend\r\n\t\t */\r\n\t\tnewNote() {\r\n\t\t\tif (this.currentNoteId !== -1) {\r\n\t\t\t\tthis.currentNoteId = -1\r\n\t\t\t\tthis.notes.push({\r\n\t\t\t\t\tid: -1,\r\n\t\t\t\t\ttitle: '',\r\n\t\t\t\t\tcontent: '',\r\n\t\t\t\t})\r\n\t\t\t\tthis.$nextTick(() => {\r\n\t\t\t\t\tthis.$refs.title.focus()\r\n\t\t\t\t})\r\n\t\t\t}\r\n\t\t},\r\n\t\t/**\r\n\t\t * Abort creating a new note\r\n\t\t */\r\n\t\tcancelNewNote() {\r\n\t\t\tthis.notes.splice(this.notes.findIndex((note) => note.id === -1), 1)\r\n\t\t\tthis.currentNoteId = null\r\n\t\t},\r\n\t\t/**\r\n\t\t * Create a new note by sending the information to the server\r\n\t\t * @param {Object} note Note object\r\n\t\t */\r\n\t\tasync createNote(note) {\r\n\t\t\tthis.updating = true\r\n\t\t\ttry {\r\n\t\t\t\tconst response = await axios.post(generateUrl('/apps/duplicatefinder/notes'), note)\r\n\t\t\t\tconst index = this.notes.findIndex((match) => match.id === this.currentNoteId)\r\n\t\t\t\tthis.$set(this.notes, index, response.data)\r\n\t\t\t\tthis.currentNoteId = response.data.id\r\n\t\t\t} catch (e) {\r\n\t\t\t\tconsole.error(e)\r\n\t\t\t\tshowError(t('notestutorial', 'Could not create the note'))\r\n\t\t\t}\r\n\t\t\tthis.updating = false\r\n\t\t},\r\n\t\t/**\r\n\t\t * Update an existing note on the server\r\n\t\t * @param {Object} note Note object\r\n\t\t */\r\n\t\tasync updateNote(note) {\r\n\t\t\tthis.updating = true\r\n\t\t\ttry {\r\n\t\t\t\tawait axios.put(generateUrl(`/apps/duplicatefinder/notes/${note.id}`), note)\r\n\t\t\t} catch (e) {\r\n\t\t\t\tconsole.error(e)\r\n\t\t\t\tshowError(t('notestutorial', 'Could not update the note'))\r\n\t\t\t}\r\n\t\t\tthis.updating = false\r\n\t\t},\r\n\t\t/**\r\n\t\t * Delete a note, remove it from the frontend and show a hint\r\n\t\t * @param {Object} note Note object\r\n\t\t */\r\n\t\tasync deleteNote(note) {\r\n\t\t\ttry {\r\n\t\t\t\tawait axios.delete(generateUrl(`/apps/duplicatefinder/notes/${note.id}`))\r\n\t\t\t\tthis.notes.splice(this.notes.indexOf(note), 1)\r\n\t\t\t\tif (this.currentNoteId === note.id) {\r\n\t\t\t\t\tthis.currentNoteId = null\r\n\t\t\t\t}\r\n\t\t\t\tshowSuccess(t('duplicatefinder', 'Note deleted'))\r\n\t\t\t} catch (e) {\r\n\t\t\t\tconsole.error(e)\r\n\t\t\t\tshowError(t('duplicatefinder', 'Could not delete the note'))\r\n\t\t\t}\r\n\t\t},\r\n\t},\r\n}\r\n</script>\r\n<style scoped>\r\n\t#app-content > div {\r\n\t\twidth: 100%;\r\n\t\theight: 100%;\r\n\t\tpadding: 20px;\r\n\t\tdisplay: flex;\r\n\t\tflex-direction: column;\r\n\t\tflex-grow: 1;\r\n\t}\r\n\r\n\tinput[type='text'] {\r\n\t\twidth: 100%;\r\n\t}\r\n\r\n\ttextarea {\r\n\t\tflex-grow: 1;\r\n\t\twidth: 100%;\r\n\t}\r\n</style>"],"sourceRoot":""}]);
